import Bmob from "hydrogen-js-sdk";
import common from '@/utils/common.js';
import producers from '@/utils/producers.js';

let uid;
let that;
let fristClassId
let secondClassId
export default {
	data() {
		return {
			producer: {},
			user: uni.getStorageSync("user"),
			identity: uni.getStorageSync("identity"),
			Images: [], //上传凭证图
			account: '',
			button_disabled: false,
			beizhu_text: "",
			real_money: 0,
			nowDay: common.getDay(0, true, true), //时间
			producerOutOrders: '',
		}
	},
	onLoad(options) {
		that = this;
		uid = uni.getStorageSync("uid");
		that.producerOutOrders = uni.getStorageSync("producerOutOrders")
		for (let item of that.producerOutOrders) {
			that.real_money += Number(item.shouldGetMoney)
		}

		uni.setNavigationBarTitle({
			title: "新增付款单"
		})
	},

	onShow() {
		that.producer = uni.getStorageSync("producer")
		that.account = uni.getStorageSync("account")

		if (uni.getStorageSync("category")) {
			that.category = uni.getStorageSync("category")

			if (that.category.type == 2) {
				let pointer2 = Bmob.Pointer('financeFristClass')
				fristClassId = pointer2.set(that.category.parent.objectId) //一级分类id关联

				let pointer3 = Bmob.Pointer('financeSecondClass')
				secondClassId = pointer3.set(that.category.objectId) //二级分类关联

			} else {
				let pointer2 = Bmob.Pointer('financeFristClass')
				fristClassId = pointer2.set(that.category.objectId) //一级分类id关联
			}
		}

	},
	methods: {

		//输入本次付款金额
		inputShouldGetMoney(e, index) {
			let inputMoney = Number(e.detail.value)
			if (inputMoney > that.producerOutOrders[index].debt) {
				that.producerOutOrders[index].shouldGetMoney = that.producerOutOrders[index].debt;
			} else {
				that.producerOutOrders[index].shouldGetMoney = e.detail.value;
			}

			that.real_money = 0;
			for (let item of that.producerOutOrders) {
				that.real_money += Number(item.shouldGetMoney)
			}
		},

		//选择时间
		bindDateChange(e) {
			that.nowDay = e.detail.value + " 00:00:00"
		},

		//移除此张照片
		removeImg(index) {
			that.Images.splice(index, 1)
			that.Images = that.Images
		},

		//上传凭证图
		upload_image() {
			if (that.user.is_vip) {
				uni.chooseImage({
					count: 3, //默认9
					sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album', 'camera'], //从相册选择
					success: function(res) {
						//console.log(res);
						let timestamp = Date.parse(new Date());
						let tempFilePaths = res.tempFilePaths
						let file;
						for (let item of tempFilePaths) {
							file = Bmob.File(timestamp + '.jpg', item);
						}
						file.save().then(res => {
							for (let item of res) {
								that.Images.push(item.url);
							}
						})
					},
				});
			} else {
				uni.showToast({
					title: "您还不是会员，无法使用",
					icon: 'none'
				})
			}
		},

		formSubmit() {
			that.button_disabled = true;
			if (uni.getStorageSync("producer") == "" || uni.getStorageSync("producer") == undefined) {
				uni.showToast({
					icon: "none",
					title: "请选择客户"
				});
				this.button_disabled = false;
				return;
			} else if (uni.getStorageSync("account") == "" || uni.getStorageSync("account") == undefined) {
				uni.showToast({
					icon: "none",
					title: "请选择付款账户"
				});
				this.button_disabled = false;
				return;
			}

			let p1 = new Promise((resolve, reject) => {
				const query = Bmob.Query('accounts');
				query.get(that.account.objectId).then(res => {
					res.set('money', res.money - Number(that.real_money));
					res.save().then(res => {
						let producer = Bmob.Pointer('producers');
						let producerID = producer.set(that.producer.objectId);
						let account = Bmob.Pointer('accounts');
						let accountID = account.set(that.account.objectId);

						let pointer = Bmob.Pointer('_User')
						let poiID = pointer.set(uid);
						let masterId = uni.getStorageSync("masterId");
						let pointer1 = Bmob.Pointer('_User')
						let poiID1 = pointer1.set(masterId);

						const query = Bmob.Query('order_opreations');
						query.set("account", accountID)
						query.set("producer", producerID)
						query.set("master", poiID);
						query.set("opreater", poiID1)
						query.set("real_money", Number(that.real_money))
						query.set("beizhu", that.beizhu_text)
						query.set('producerOutOrders', that.producerOutOrders)
						//query.set("debt", that.producer.debt || 0)
						query.set("createdTime", {
							"__type": "Date",
							"iso": that.nowDay
						}); // 操作单详情
						query.set("type", 1);
						query.set("extra_type", 5);
						query.set("Images", that.Images);
						query.save().then(res => {
							
							common.log(uni.getStorageSync("user").nickName + "操作'" + that.producer.producer_name + "'供应商付款了￥" + that.real_money +
								"元", 6, res.objectId);
								resolve("success")
						}).catch(err => {
							console.log(err)
						})
					})
				})
			})


			let p2 = new Promise((resolve, reject) => {
				for (let item of that.producerOutOrders) {
					const query = Bmob.Query('order_opreations');
					query.set('id', item.objectId) //需要修改的objectId
					query.set('debt', Number(item.debt) - Number(item.shouldGetMoney))
					query.set('haveGetMoney', Number(item.haveGetMoney) + Number(item.shouldGetMoney))
					query.save().then(res => {
						resolve("success")
						console.log(res)
					}).catch(err => {
						console.log(err)
					})
				}
			})

			Promise.all([p1, p2]).then((result) => {
				console.log(result) //['成功了', 'success']
				this.button_disabled = false;
				uni.navigateBack({
					delta: 1
				})

				setTimeout(function() {
					uni.showToast({
						icon: "none",
						title: '付款成功',
					})
				}, 1000)
			}).catch((error) => {
				console.log(error)
			})

		},

	}
}
